.gamification {
    background-color: #f0f4f8;
    border-radius: 10px;
    padding: 20px;
    width: 300px;
    margin: 20px auto;
    text-align: center;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.h2 {
    font-size: 24px;
    color: #333;
    margin-bottom: 10px;
}

.p {
    font-size: 18px;
    color: #555;
    margin: 5px 0;
}

.button {
    background-color: #4CAF50;
    color: white;
    font-size: 16px;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.button:hover {
    background-color: #45a049;
}

.badge {
    margin-top: 15px;
    font-size: 18px;
    font-weight: bold;
    color: #ff9800;
    padding: 5px 10px;
    background-color: #fff3e0;
    border-radius: 5px;
    display: inline-block;
}

.badge span {
    color: #ff5722;
    font-size: 20px;
}

